* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  padding-left: 0px;
}
.country-item {
  display: flex;
  align-items: center;
}
.country-image {
  margin-right: 5px;
}

.lang-list {
  padding-left: 0;
}
.country-name {
  font-weight: 600;
  margin: 4px;
}
.country-title {
  font-size: 20px;
  font-weight: 700;
}